<template>
  <v-card elevation="0" :disabled="loading" :loading="loading">
    <BlockItem>
      <template v-slot:icon>
        mdi-wall
      </template>
      <template v-slot:title>
        اسپانسر ها
      </template>

      <v-form ref="form">
        <v-row class="px-3" v-if="renderComponent" >
          <v-col class="py-0" cols="12" md="3">
            <v-text-field
                outlined
                dense
                :rules="[$store.state.rules.required(data.name,'نام')]"
                v-model="data.name"
                append-icon="mdi-phone-hangup-outline"
                label="نام"
                color="txtf"
            >

            </v-text-field>
          </v-col>
          <v-col class="py-0" cols="12" md="3">
            <v-text-field
                outlined
                dense
                :rules="[$store.state.rules.required(data.website_link,'لینک وب سایت')]"
                v-model="data.website_link"
                append-icon="mdi-link"
                label="لینک وب سایت"
                color="txtf"
            >

            </v-text-field>
          </v-col>
          <v-col class="py-0" cols="12" md="3">
            <v-file-input
                outlined
                dense
                :rules="[$store.state.rules.required(data.image,'تصویر')]"
                v-model="data.image"
                label="تصویر کمپانی"
                color="txtf"
            >

            </v-file-input>
          </v-col>
          <v-col class="py-0" cols="12" md="3">
            <v-btn class="dash-btns dash-btns--blue d-block mx-auto" :loading="loading" dark height="40"
                   @click="createC()">افزودن اسپانسر
            </v-btn>
          </v-col>

          <v-col cols="12" >
            <v-row>
              <v-col v-for="(item,index) in items" :key="index+'clogosc'" cols="6" sm="4" md="2" >
                <v-tooltip
                color="black"
                bottom
                >
                  <template v-slot:activator="{on}">
                    <v-img v-on="on" width="100%" contain max-height="60" :src="item.image" :lazy-src="item.thumbnail" >
                      <a-sbtn @clickBtn="toDelete(item.id,'/website/api/v1/website/delete-sponsor/')" color="#DC3545" icon="mdi-close" alt="حذف کمپانی" />
                    </v-img>
                  </template>
                  {{item.website_link}}
                </v-tooltip>

              </v-col>
            </v-row>
          </v-col>

        </v-row>
      </v-form>

    </BlockItem>
    <template v-slot:progress>
      <GDLoading/>
    </template>

    <DeleteDialog :ddata="deleteData" @deleteFn="fetchSpanser()" ></DeleteDialog>

  </v-card>
</template>

<script>
import BlockItem from "@/components/Misc/BlockItem";
import GDLoading from "@/components/Dashboard/Misc/GDLoading";
import {isChars} from "@/components/Misc/isChars";
import axios from "axios";
import ASbtn from "@/components/Dashboard/Misc/Components/a-sbtn";
import DeleteDialog from "@/components/Misc/DeleteDialog";
import {forceRerender} from "@/components/Misc/forceRerender";

export default {
  name: "CompanyDash",
  components: {DeleteDialog, ASbtn, GDLoading, BlockItem},
  created() {
    this.fetchSpanser()
  },
  mixins: [isChars,forceRerender],
  data() {
    return {
      deleteData: {
        api : '',
        dialog: false,
        loading: false,
        idSelected: null,
      },
      loading: false,
      data: {
        name: null,
        website_link: null,
        image: null,
      },
      items : []
    }
  },
  methods: {
    toDelete(id,api) {
      this.deleteData.idSelected = id;
      this.deleteData.api = api;
      this.deleteData.dialog = true;
    },
    createC() {
      this.$refs.form.validate();
      if (this.$refs.form.validate()) {
        if(this.data.image.size>2097152) {
          this.notify('حجم عکس بیشتر از 2 م.گ','error')
          return
        }
        if((this.data.image.type!=='image/jpeg') && (this.data.image.type!=='image/jpg') && (this.data.image.type!=='image/png')) {
          this.notify('فرمت غیر قابل پشتیبانی','error')
          return
        }
        this.loading = true;
        let formData = new FormData();
        formData.append('image', this.data.image);
        formData.append('name', this.data.name);
        formData.append('website_link', this.data.website_link);
        this.data = {
              name: null,
              website_link: null,
              image: null,
        };
        this.forceRerender();
        axios.post(this.$store.state.host + '/website/api/v1/website/create-sponsor', formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
            Accept: "*/*",
            Authorization: `Bearer ${this.$store.state.userId}`,
          }
        }).then(() => {
          this.notify('اسپانسر افزوده شد.', 'success');
          this.fetchSpanser();
        }).catch(error => {
          this.errorHandle(error.response)
        }).finally(() => {
          this.loading = false;
        })
      }
    },
    fetchSpanser() {
      this.loading = true;
      axios.get(this.$store.state.host + '/website/api/v1/website/sponsors').then(response => {
        this.items = response.data.data;
      }).catch(error => {
        this.errorHandle(error.response)
      }).finally(() => {
        this.loading = false;
      })
    }

  }
}
</script>

